"use client"

import Link from "next/link"
import { usePathname } from "next/navigation"

const Sidebar = () => {
  const pathname = usePathname()

  const isActive = (path: string) => {
    return pathname === path ? "bg-gray-800 text-white" : "text-gray-300 hover:bg-gray-700"
  }

  return (
    <div className="w-64 bg-gray-900 text-white">
      <div className="p-4">
        <h1 className="text-2xl font-bold mb-8">Brick Industry</h1>
        <nav className="space-y-2">
          <Link href="/" className={`block p-3 rounded-lg ${isActive("/")}`}>
            Dashboard
          </Link>
          <Link href="/sales" className={`block p-3 rounded-lg ${isActive("/sales")}`}>
            Sales Records
          </Link>
          <Link href="/orders" className={`block p-3 rounded-lg ${isActive("/orders")}`}>
            Orders
          </Link>
          <Link href="/labour" className={`block p-3 rounded-lg ${isActive("/labour")}`}>
            Labour Book
          </Link>
        </nav>
      </div>
    </div>
  )
}

export default Sidebar
